'use client';
import { useAuth } from '@/lib/firebase';
import Image from 'next/image';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { Button } from '../ui/button';

export function AdminHeader() {
  const { logout } = useAuth();
  const router = useRouter();

  const handleLogout = async () => {
    await logout();
    router.push('/login');
  };

  return (
    <header className="bg-card border-b p-4 flex justify-between items-center">
      <div className="flex items-center gap-4">
        <Link href="/admin" className="flex items-center gap-2">
          <Image
            src="https://placehold.co/40x40.png"
            width={40}
            height={40}
            alt="TumbleBunnies Corporate Logo"
            data-ai-hint="corporate logo"
          />
          <span className="font-headline text-2xl font-bold">TumbleBunnies</span>
        </Link>
        <Link href="/admin-merchandise" className="text-primary font-semibold hover:underline">
          Manage Merchandise
        </Link>
      </div>
      <Button onClick={handleLogout} variant="outline">
        Logout
      </Button>
    </header>
  );
}
